@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap%27);
:root{
  --text-color-red: #F8EBEB;
  --text-color-yellow: #FFF8E0;
  --text-color-green: #F1FFE0;
  --background-color-red: #EB9191;
  --background-color-yellow: #EBD791;
  --background-color-green: #B1D188;
  --error-color-red: #AA3737;
  --error-color-yellow: #9A7B0E;
  --error-color-green: #49701A;
  

}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}

/* COMMON STYLING */
p, h1, h2, h3{
	/* font-family: 'Roboto', sans-serif; */
}
.text-red{
  color: #F8EBEB;
  color: var(--text-color-red);
}
.text-yellow{
  color: #FFF8E0;
  color: var(--text-color-yellow);
}
.text-green{
  color: #F1FFE0;
  color: var(--text-color-green);
}
.background-red{
  background-color: #EB9191;
  background-color: var(--background-color-red);
}
.background-yellow{
  background-color: #EBD791;
  background-color: var(--background-color-yellow);
}
.background-green{
  background-color: #B1D188;
  background-color: var(--background-color-green);
}
.button-red{
  background-color: #F8EBEB;
  background-color: var(--text-color-red);
  color:#AA3737;
  color:var(--error-color-red);
}
.button-yellow{
  background-color: #FFF8E0;
  background-color: var(--text-color-yellow);
  color:#9A7B0E;
  color:var(--error-color-yellow);
}
.button-green{
  background-color: #F1FFE0;
  background-color: var(--text-color-green);
  color:#49701A;
  color:var(--error-color-green);
}
.error-red{
  color: #AA3737;
  color: var(--error-color-red);
}
.error-yellow{
  color: #9A7B0E;
  color: var(--error-color-yellow);
}
.error-green{
  color: #49701A;
  color: var(--error-color-green);
}
.input-background-red{
  background-color: #F8EBEB;
  background-color: var(--text-color-red);
}
.input-background-yellow{
  background-color: #FFF8E0;
  background-color: var(--text-color-yellow);
}.input-background-green{
  background-color: #F1FFE0;
  background-color: var(--text-color-green);
}
/* END */

button{
  cursor: pointer;
}
/* Global css */
.App {
	height: 100vh;
	width:100vw;
	overflow:hidden;
}
.logoBig {
	width:30em;
	height:10em;
	margin: 3em;
}
.start-card-wrapper{
	width: 20em;
	display: flex;
	height: 125vh;
	flex-direction: column;
	margin: 0 auto;
	padding-top:25em;
}
.card{
	height:95%;
	margin-top: -18em;
}

.start-card-wrapper input[type="radio"]:checked + label {
	flex: 1 1 auto;
	margin-bottom: -3em;
}

.start-card-wrapper  label {
	flex: 0 0 auto;
	transition: all 1s;
}

/* DISPLAY NONE I DESKTOP VY på ALLT utom korten */
.start-card-wrapper > input	{
	display: none;
}
@media only screen and (max-height: 580px){
	.start-card-wrapper{
		height:150vh;
		width:18em;
		padding-top:23em;
	}
	.start-card-wrapper input[type="radio"]:checked + label {
		margin-bottom: -2em;
	}
	.card{
		margin-top: -16.3em;
		height:auto;
	}
}
@media only screen and (max-height: 700px){
	.start-card-wrapper{
		height:140vh
	}
}
@media only screen and (min-width: 700px){
	.start-card-wrapper{
		position: relative;
		display:grid;
		width:80%;
		grid-template-columns: repeat(3,1fr);
		margin:0 auto;
		padding-top: 1rem;
	}
	.first-card{
		grid-column-start: 1;
		grid-column-end: 1;
		z-index: 10;
		margin-top: 0;
	}
	.second-card{
		grid-column-start: 2;
		grid-column-end: 2;
		z-index: 5;
		margin-top: 0;
	}
	.third-card{
		grid-column-start: 3;
		grid-column-end: 3;
		z-index: 1;
		margin-top: 0;
	
	}

}
@media only screen and (min-width: 1200px){
	.start-card-wrapper{
		width:60%;
		padding-left: 3em;
	}
}

.title-container{
	display: flex;
	flex-direction: row;
	justify-content: center;
	padding: 2em 0.5em 1em;
}
.title-container .title{	
	width:90%;
	height:40px;
	line-height: 40px;
}
.title-container .icon{
	align-self: flex-end;
}
.start-card-container{
	height:100%;
}
.start-card{
	display: flex;
	min-height: 23em;
	flex-direction: column;
	justify-content: space-around;
    padding: 0 2em 3em;
	border-radius:25px;
	height:100%;
