}
.list-item{
	padding: 0.2em;
}
.btn-container{
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: auto 0 0
}
.btn-list{
	font-size: 1rem;
    width: 100%;
    border: none;
    margin-top: 1.5rem;
    padding: .5em;
	border-radius: 10px;
	transition: transform ease-in 500ms;
	cursor: pointer;
}
.btn-add{
	font-size: 1rem;
    width: 100%;
    border: none;
    margin-top: 0.8rem;
    padding: .5em;
	border-radius: 10px;
	transition: transform ease-in 500ms;
	cursor: pointer;
}
@media only screen and (max-height: 580px){
	.start-card{
		min-height: 19em;
	}
	.listStuff{
		max-height: 6rem;
		overflow: scroll;
		
	}
}
