	.start-card{
		margin-left: -3em;
		min-height: 27em;
		max-height: 27em;
	}
	.start-card:hover {
		transform: translateY(-20px);
		transition: 0.4s ease-out;
	}
	.btn-list{
		width: 70%
	}
	.btn-add{
		width: 70%
	}
	.btn-add:hover, .btn-list:hover{
		transform: scale(1.2);
	}
	
