	.form-textarea-container span{
		padding-left: 0.6em;
    align-self: start;
    position: unset;
	}
	
  form textarea{
      width: 60%;
      max-width: 31rem;
      height: 7rem;
  }
  form > input[type="submit"]{
    bottom: 2rem;
    right: 2rem;
    position: absolute;
    padding: 0.8em;
    border-radius: 5px;
    border: none;
    width: 10rem;
    font-weight: bolder;
    font-size: 1rem;
  }
}

@media(max-height: 800px) and (min-width: 700px){
  form > div{
    padding: 0;
  }
  form textarea{
    max-height: 8rem;
  }
  form label{
    padding-bottom: unset;
    margin-top: .2rem;
  }
}
